export interface ITodo {
    value?: IOfferData[];
    MSProduct?: IDDLData[];
    ServicesDomain?: IDDLData[];
}

export interface IOfferData {
    Description: string;
    FileCount: number;
    Title: string;
    SrNo: number;
}

export interface IDDLData {
    value: string;
    count: number;
}

export default class GraphService {
    private offerURL: any;
    private documentURL: any;
    constructor() {
        this.offerURL = "https://idr.search.windows.net/indexes/offers/docs/?api-version=2016-09-01&search=%2F.*`searchTextReplace`.*%2F&$top=500&$filter=search.in(ReleaseStatus,'New,Active,Retiring Soon,Updated',',')&facet=Title,count:50,sort:value&facet=MSProducts,count:50,sort:value&facet=VerticalIndustries,count:50,sort:value&facet=ServicesDomain,count:50,sort:value&$count=true";
        this.documentURL = "https://microsoft.sharepoint.com/sites/Campus/_api/search/query?querytext='azure (((path:https://microsoft.sharepoint.com/teams/campusiplibraries* OR path:https://microsoft.sharepoint.com/teams/CampusProjectSites*  OR (departmentid:{6c66197b-675f-4f6c-8003-9a7f8a434c07})) AND ((contentclass:STS_ListItem_DocumentLibrary OR ContentTypeId:0x01010A*) NOT(ContentType:Folder))) OR (((path:https://microsoft.sharepoint.com/teams/campusipkit* OR (departmentid:{6c66197b-675f-4f6c-8003-9a7f8a434c07})) AND IPKitIsActiveOWSBool=1) ANDNOT (ContentType:Folder) ) OR (path:https://microsoft.sharepoint.com/teams/CampusCOPs/CSA*))'&processpersonalfavorites=false&clienttype='Campus'&rowlimit=500&refiners='MSVerticalIndustry,CampusServicesDomain,MSProducts,MSIPTypes,ServicesBusinessScenario,ServicesDevelopmentLanguages,FileType,MSLanguage,SPContentType,DisplayAuthor,CampusPeerReviewIndicator(deephits=100000,discretize=manual/2/3/4/5),CampusSMEReviewIndicator(deephits=100000,discretize=manual/2/3/4/5),LastModifiedTime'&selectproperties='Title,Description,Path,ViewsLifeTime,LastModifiedTime,MSVerticalIndustry,CampusServicesDomain,MSProducts,MSIPTypes,ServicesBusinessScenario,ServicesDevelopmentLanguages,FileType,MSLanguage,SPContentType,DisplayAuthor,CampusPeerReviewIndicator,CampusPeerReviewCount,CampusSMEReviewIndicator,CampusSMEReviewCount,DlcDocId,UIVersionStringOWSTEXT,Author,DocId'";
    }

    public getData = (token) => {
        const headers = new Headers({ Accept: "application/json; odata=verbose", "api-key": "FF41C4DDEA9E2249E84B27AD9569F102", "x-ms-azs-return-searchid": "true", "Access-Control-Expose-Headers": "x-ms-azs-searchid" });
        const options = {
            headers
        };

        const headers1 = new Headers({ Accept: "application/json; odata=verbose", Authorization: `Bearer ${token}` });
        const options1 = {
            headers: headers1
        };

        Promise.all([
            fetch(this.offerURL, options),
            fetch(this.documentURL, options1)
        ]).then((responses) => {
            // Get a JSON object from each of the responses
            return Promise.all(responses.map((response) => {
                return response.json();
            }));
        }).then((data) => {
            // Log the data to the console
            // You would do something with both sets of data here
            if (data && data.length > 1) {
                console.log(data);
            }
        }).catch((error) => {
            // if there's an error, log it
            console.log(error);
        });
    }
}
